<template>
  <div style="margin: 0 10px;" class="collection">
    <div class="rg-top-top">
      <div>
        <span class="content-top-text-blue"></span>
        <strong style="margin-left: 2px">{{company[0].shenbao_month}} 税款计算</strong>
        <span class="f-14 ml-50" style="font-weight: normal">总申报人数：</span>
        <span id="top_sum">{{allPersonNumber}}</span>
        <span class="f-14 mr-20" style="font-weight: normal">人</span>
        <span class="f-14" style="font-weight: normal">本期总收入额：</span>
        <span id="top_add">{{all.allIncome}}</span>
        <span class="f-14 mr-20" style="font-weight: normal">元</span>
        <span class="f-14" style="font-weight: normal">应纳税额：</span>
        <span>{{all.allTax}}</span>
        <span class="f-14 mr-20" style="font-weight: normal">元</span>
        <span class="f-14" style="font-weight: normal">应补退税额：</span>
        <span>{{all.allTuibu}}</span>
        <span class="f-14" style="font-weight: normal">元</span>
      </div>
      <div class="top-bottom">
        <div>
          <el-button type="primary" size="small">重新计算</el-button>
        </div>
        <div class="top-bottom-right">
          <span class="weight">算税方式 :</span>
          <span>在线算税</span>
          <el-button type="primary" size="small">导出</el-button>
          <el-button type="primary" size="small">展开条件查询</el-button>
        </div>
      </div>
    </div>
    <div class="rg-nav">
      <div>
        所得项目：
        <el-select
          v-model="selectValue"
          placeholder="请选择"
          style="width:200px"
          @change="selectProject"
        >
          <el-option label="正常工资薪金所得" value="正常薪金所得"></el-option>
          <el-option value="全年一次性奖金收入"></el-option>
          <el-option label="劳务报酬所得(一般劳务、其他劳务)" value="劳务所得"></el-option>
        </el-select>
      </div>
      <div>
        <span class="f-14 ml-50" style="font-weight: normal">纳税总人数：</span>
        <span>{{allPersonNumber}}</span>
        <span class="f-14 mr-20" style="font-weight: normal">人</span>
        <span class="f-14" style="font-weight: normal">本期收入总额：</span>
        <span>{{all.allIncome}}</span>
        <span class="f-14 mr-20" style="font-weight: normal">元</span>
        <span class="f-14" style="font-weight: normal">应补退税额：</span>
        <span>{{all.allTuibu}}</span>
        <span class="f-14" style="font-weight: normal">元</span>
      </div>
    </div>
    <div style="border: solid 1px #e0e0e0; width:calc(100%)">
      <div id="con_in" class="con-div" style="overflow-y: auto; display: block;">
        <el-table :data="allPersonArr" border>
          <el-table-column type="selection"></el-table-column>
          <el-table-column prop="employmentNumber" label="工号"></el-table-column>
          <el-table-column prop="personName" label="姓名"></el-table-column>
          <el-table-column prop="cardType" label="证照类型"></el-table-column>
          <el-table-column prop="cardNumber" label="证照号码" width="200"></el-table-column>
          <el-table-column label="所得项目" prop="incomeProject" width="180"></el-table-column>
          <el-table-column prop="incomeAll" label="累计收入"></el-table-column>
          <el-table-column prop="incomeNoTaxAll" label="累计免税收入" width="110"></el-table-column>
          <el-table-column prop="otherAll.jianchufeiyongbiaozhunAll" label="累计减除费用" width="110"></el-table-column>
          <el-table-column prop="jianchuAll" label="累计扣除及减除项目合计" width="180"></el-table-column>
          <el-table-column prop="otherAll.zhunyukouchuAll" label="累计准予扣除的捐赠额" width="170"></el-table-column>
          <el-table-column prop="leijiNashuiAll" label="累计应纳税所得额" width="140"></el-table-column>
          <el-table-column prop="taxAll.shuilv" label="税率/预扣率" width="100"></el-table-column>
          <el-table-column prop="taxAll.susuan" label="速算扣除数" width="100"></el-table-column>
          <el-table-column prop="taxAll.taxPay" label="累计应纳税额" width="120"></el-table-column>
          <el-table-column prop="otherAll.jianmianshuieAll" label="累计减免税额" width="120"></el-table-column>
          <el-table-column prop="yingkoujiaoshuie" label="累计应扣缴税额" width="140"></el-table-column>
          <el-table-column prop="otherAll.yikoujiaoshuie" label="已扣缴税额" width="100"></el-table-column>
          <el-table-column prop="tuibu" label="应补(退)税额" width="100"></el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import dataHelper from '@/until/dataHelper'

export default {
  data () {
    return {
      allIncomeTable: [], // 综合所得预扣预缴表
      selectValue: '',
      tableDataIn: []
    }
  },
  computed: {
    ...mapGetters(['company']),
    allPersonNumber () {
      return this.$store.getters.getAllPersonTax.length
    },
    allPersonArr () {
      let a = dataHelper.tableRelevanceArr(this, this.$store.getters.getAllPersonTax, this.$store.getters.getFormContent('personCollection'), ['personName', 'cardNumber', 'cardType', 'employmentNumber'], 'personInfoId', 'infoId')
      let b = a
      console.log(a)
      if (this.selectValue) {
        b = []
        for (let i in a) {
          if (a[i].incomeProject === this.selectValue || (a[i].incomeProject === '一般劳动项目所得' && this.selectValue === '劳务所得') || (a[i].incomeProject === '其他劳动项目所得' && this.selectValue === '劳务所得')) {
            b.push(a[i])
          }
        }
      }
      return b
    },
    all () {
      let a = this.$store.getters.getAllPersonTax
      console.log(a)
      let all = {}
      all.allIncome = 0.00
      all.allTax = 0.00
      all.allTuibu = 0.00
      for (let i in a) {
        if (a[i].incomeAll)all.allIncome += parseFloat(a[i].income)
        if ((a[i].taxAll.taxPay)) all.allTax += parseFloat(a[i].taxAll.taxPay)
        if ((a[i].tuibu))all.allTuibu += parseFloat(a[i].tuibu)
      }
      return all
    }

  },
  created () {
    this.$emit('router', this.$router.history.current.name)
    this.$emit('pathNumber', 2)
  },
  mounted () {

  },
  methods: {
    showNull () {
      this.$alert('本系统为教学系统，目前只提供常用操作功能噢', '信息', {
        confirmButtonText: '确定'
      })
    },
    nullClick () {
      this.showNull()
    },
    selectProject (a) {
      console.log(a)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1,
  h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  .el-table {
    font-size: 14px;
  }

  .rg-top-top {
    overflow: hidden;
    line-height: 40px;
    box-sizing: border-box;
    margin-top: 10px;
    padding: 5px 15px;
    border: 1px solid #e0e0e0;
  }

  .top-bottom {
    display: flex;
    justify-content: space-between;
  }

  .weight {
    font-size: 16px;
    font-weight: 700;
  }

  .top-bottom-right :nth-child(2) {
    color: #4285f4;
    font-size: 14px;
  }

  .top-bottom-right :nth-child(3) {
    margin: 0 10px;
  }

  .rg-nav {
    display: flex;
    justify-content: space-between;
    padding: 15px 0;
  }

  .el-table .cell {
    width: auto;
  }
</style>



